
body{
	margin: 0
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.main{
	height: 100vh
}
h1{
  font-size: 120px
}
#title{
  font-size: 50px;
  border: none;
  text-align: center;
  color: #2c3e50;
  margin-top: 50px;
  outline: none
}
.list-layout{
	position: absolute;
	text-align: center;
	width: 100%;
	bottom: 20px;
}
.list{
	list-style: none;
	display: table;
	margin: 20px auto;
	padding: 0
}
.list li{
	font-size: 32px;
	margin: 0 20px;
	float: left;
	text-align: center
}
.control{
	margin: 50px 0
}
.list-move {
  -webkit-transition: -webkit-transform 1s;
  transition: -webkit-transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
}
.list-enter-active, .list-leave-active {
  -webkit-transition: all 1s;
  transition: all 1s;
}
.list-enter, .list-leave-active {
  opacity: 0;
  -webkit-transform: translateY(30px);
          transform: translateY(30px);
}



